.subfooter {
    padding-top: 61px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 20px; /* Adjust spacing between items */
}

.footer-item {
    flex: 1; /* Distribute available space evenly among items */
    max-width: 385px; /* Set a maximum width for each item */
    margin-top: 20px; /* Adjust the margin to your preference */

}

/* Adjust flex-grow for the last item to make it longer */
.footer-item:last-child {
    flex-grow: 2; /* Increase the flex-grow value for the last item */
}

