    overflow: hidden;
    z-index: 1;
    height: 398px;
    width: 349px;
  }
  
  .services-thumb-two:after,
  .services-thumb-two:before {
    content:"";
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100%
  }
  
  .services-item-two:hover .services-thumb-two:after {
    opacity:1
  }
  
  .services-thumb-two .item-shape {
    position:absolute;
    right:0;
    top:0;
    margin-top:-50px;
